.App {
  display: flex;
  justify-content: center;
  font-size: 14px;
}
.user-prompt {
  .user-input {
    margin-top: 8px;
    width: 100%;
  }
}
.chat {
  padding-top: 48px;
  width: 224px;
  .messages {
    max-height: 300px;
    overflow: auto;
    .message {
      padding: 6px 0;
      .user {
        font-weight: bold;
        font-size: 13px;
      }
    }
  }
  .input {
    width: 100%;
  }
}
